*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  -webkit-tap-highlight-color: transparent;
}

body {
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  line-height: 1.1;
  // font-weight: normal;
}

p {
  margin-top: 0;
}

ul,
ol {
  margin-top: 0;
  ul,
  ol {
    margin-bottom: 0;
  }
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

dl {
  margin-top: 0;
}

dd {
  margin: 0;
}

blockquote {
  p,
  ul,
  ol {
    &:last-child {
      margin-bottom: 0;
    }
  }
}

figure {
  margin: 0;
}

address {
  font-style: normal;
}

form {
  margin: 0;
}

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

textarea {
  resize: vertical;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  cursor: pointer;
}

[disabled] {
  cursor: default;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

input[type="submit"] {
  background: none;
  border: none;
  font-size: 1em;
}

.element-invisible {
  display: none !important;
}


/* ==========================================================================
   Print styles.
   https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css
   ========================================================================== */
@media print {
  *,
  *:before,
  *:after,
  *:first-letter,
  *:first-line {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }

  a,
  a:visited {
    text-decoration: underline;
  }

  a[href]:after {
    content: " (" attr(href) ")";
  }

  abbr[title]:after {
    content: " (" attr(title) ")";
  }

  a[href^="#"]:after,
  a[href^="javascript:"]:after {
    content: "";
  }

  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }

  thead {
    display: table-header-group;
  }

  tr,
  img {
    page-break-inside: avoid;
  }

  img {
    max-width: 100% !important;
  }

  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }

  h2,
  h3 {
    page-break-after: avoid;
  }
}
